import request from '@/utils/request'
// api
// 
export function getCustomerStoreList(params) {
  return request({
    url: '/crm/customer/list',
    method: 'post',
    data: params
  })
}
// 
export function getRelated(params) {
  return request({
    url: '/crm/user/list',
    method: 'get',
    params: params
  })
}
// 
export function getRelatedInfoById(id) {
  return request({
    url: `/crm/user/${id}`,
    method: 'get',
    params: id
  })
}
// 
export function getCustomerInfoById(id) {
  return request({
    url: `/crm/customer/${id}/detail`,
    method: 'get',
    params: id
  })
}
// 
export function addCustomer(params) {
  return request({
    url: '/crm/customer',
    method: 'post',
    data: params
  })
}
// 
export function updateCustomer(params, id) {
  return request({
    url: `/crm/customer/${id}/update`,
    method: 'put',
    data: params
  })
}
//
export function customerRelatedUser(params, id) {
  return request({
    url: `/crm/customer/${id}/relative`,
    method: 'put',
    data: params
  })
}
// 
export function putSourceChannel(params, type) {
  const url =
    type === 'status'
      ? `/crm/basic/source_channel/${params._id}/status`
      : `/crm/basic/source_channel/${params._id}`
  return request({
    url: url,
    method: 'put',
    data: params
  })
}
// option
export function getSourceChannelOption(params) {
  return request({
    url: `/crm/basic/source_channel/options`,
    method: 'get',
    params: params
  })
}
// 
export function getSourceChannelList(params) {
  return request({
    url: '/crm/basic/source_channel/list',
    method: 'post',
    data: params
  })
}

