<template>
  <div v-if="serviceInfo">
    <p class="margin-top-20 margin-bottom-6">
      
    </p>
    {{ fdProduct ? fdProduct.name : '-' }}
    <el-tooltip class="package" effect="dark" placement="top">
      <div slot="content">
        <div class="package-detail">
          <div v-if="department !== 'chanke'">
            {{ serviceInfo.doctor ? serviceInfo.doctor.name : '' }}
          </div>
          <div v-if="department !== 'chanke'">
            {{
              serviceInfo.dietician ? serviceInfo.dietician.name : ''
            }}
          </div>
          <div>
            {{
              department === 'chanke' ? '' : ''
            }}
            {{ serviceInfo.hm ? serviceInfo.hm.name : '' }}
          </div>
          <p class="color-blue" style="margin:10px 0 0;">
            
          </p>
          <div v-if="serviceTransfer.length">
            <div v-for="(item, index) in serviceTransfer" :key="index">
              <p v-if="serverFilter(item).key">
                <span style="display:inline-block;min-width:150px;">
                  {{ serverFilter(item).key }}
                </span>
                <span> {{ serviceInfo[item] }}</span>
              </p>
            </div>
          </div>
          <div>
            <p style="margin:10px 0 0;"></p>
            <span
              v-for="(service_items, index) in serviceInfo.service_items"
              :key="'service_items' + index"
            >
              {{ service_items }}</span
            >
          </div>
        </div>
      </div>
      <i class="el-icon-info" />
    </el-tooltip>
    <div>
      
      {{ formatTime(serviceInfo.started_at, '{y}-{m}-{d}') || '' }}{{
        formatTime(serviceInfo.ended_at, '{y}-{m}-{d}') || ''
      }}
    </div>
  </div>
</template>
<script>
export default {
  props: {
    serviceInfo: {
      type: Object,
      default: () => {}
    },
    fdProduct: {
      type: Object,
      default: () => {}
    },
    department: {
      type: String,
      default: 'erke'
    }
  },
  data() {
    return {
      serviceTransferNull: true, //
      serviceTransfer: []
    }
  },
  created() {
    this.getServiceByDepartment()
  },
  watch: {
    department(val) {
      this.getServiceByDepartment()
    }
  },
  methods: {
    getServiceByDepartment() {
      if (this.department === 'chanke') {
        this.serviceTransfer = [
          'total',
          'month',
          'common_text_total',
          'common_text_month',
          'common_voice_total',
          'common_voice_month'
        ]
      } else if (this.department === 'erke') {
        this.serviceTransfer = [
          'total',
          'month',
          'doctor_text_total',
          'doctor_text_month',
          'dietician_text_total',
          'dietician_text_month',
          'doctor_voice_month',
          'doctor_voice_total'
        ]
      }
    },
    serverFilter(key) {
      const obj = {
        key: ''
      }
      switch (key) {
        case 'total':
          if (this.department === 'chanke') {
            obj.key = ''
          } else {
            obj.key = ''
          }
          break
        case 'month':
          if (this.department === 'chanke') {
            obj.key = ''
          } else {
            obj.key = ''
          }
          break
        case 'doctor_text_total':
          obj.key = ''
          break
        case 'doctor_text_month':
          obj.key = ''
          break
        case 'dietician_text_total':
          obj.key = ''
          break
        case 'dietician_text_month':
          obj.key = ''
          break
        case 'doctor_voice_month':
          obj.key = ''
          break
        case 'doctor_voice_total':
          obj.key = ''
          break
        // 
        case 'common_text_total':
          obj.key = ''
          break
        case 'common_text_month':
          obj.key = ''
          break
        case 'common_voice_total':
          obj.key = ''
          break
        case 'common_voice_month':
          obj.key = ''
          break
        default:
          return obj
      }
      return obj
    }
  }
}
</script>
